import { Select, SelectProps } from "antd";

export type StrictSelectProps = Pick<
  SelectProps,
  | "value"
  | "onChange"
  | "options"
  | "defaultValue"
  | "placeholder"
  | "showSearch"
  | "filterOption"
  | "filterSort"
  | "onSearch"
  | "notFoundContent"
  | "suffixIcon"
  | "loading"
>;

const FilterSelect = (props: StrictSelectProps) => {
  return <Select allowClear {...props} />;
};

export default FilterSelect;
